import http from 'src/utils/http';
export function getGroupList() {
  return http.get(`/datashare/groups`);
}
export function addGroup(params) {
  return http.post(`/datashare/group`, params);
}
export function editGroup({params, groupId}) {
  return http.put(`/datashare/group/${groupId}`, params);
}
export function deleteGroup(id) {
  return http.delete(`/datashare/group/${id}`);
}
export function getApiList(params) {
  return http.post(`/datashare/${params.guid}/${params.belong}/${params.publishState}`, {
    query: params.query,
    offset: params.offset,
    limit: params.limit
  });
}
export function publishApi({ids, status}) {
  return http.put(`/datashare/publish`, ids);
}
export function unPublishApi({ids, status}) {
  return http.delete(`/datashare/publish`, {data: ids});
}
export function collectApi({id, status}) {
  return http.put(`/datashare/star/${id}`);
}
export function unCollectApi({id, statue}) {
  return http.delete(`/datashare/star/${id}`);
}
export function getDataBase(params) {
  return http.post(`/datashare/databases`, params);
}
export function getTable(params) {
  return http.post(`/datashare/tables/${params.dbGuid}`, {
    query: params.query,
    offset: params.offset,
    limit: params.limit
  });
}
export function getColumn(tableGuid) {
  return http.get(`/datashare/table/columns/${tableGuid}`);
}
export function testApi({params, randomName}, cancelToken) {
  return http.post(`/datashare/test/${randomName}`, params, {cancelToken});
}
export function stopTestApi(randomName) {
  return http.put(`/datashare/test/${randomName}`);
}
export function createApi(params) {
  return http.post(`/datashare`, params);
}
export function editApi({params, apiId}) {
  return http.put(`/datashare/${apiId}`, params);
}
/**
 * api detail
 */
export function delApi(id) {
  return http.delete(`/datashare/${id}`);
}

export function getApiDetail(guid) {
  return http.get(`/datashare/${guid}`);
}



