import types from 'src/store/mutation-types';
import {
  LOADING,
  SUCCESS,
  FAIL
} from 'src/store/status';
import * as apiService from 'src/service/data-share';
const {
  STORE_API_MSG,

  GET_API_DETAIL,
  GET_API_DETAIL_SUCCESS,
  GET_API_DETAIL_FAILURE,

  GET_GROUP_LIST,
  GET_GROUP_LIST_SUCCESS,
  GET_GROUP_LIST_FAILURE
} = types;

// initial state
const state = {
  storeApiMsg: {},

  apiDetail: {
    data: {},
    status: SUCCESS
  },
  sourceGroupTree: [],
  sourceGroupTreeStatus: SUCCESS
};

const actions = {
  async saveApiMsg(context, params) {
    const {
      commit
    } = context;

    try {
      commit(STORE_API_MSG, params);
    } catch (err) {
      throw err;
    }
  },
  async getApiDetail(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_API_DETAIL, params);
      const res = await apiService.getApiDetail(params);
      commit(GET_API_DETAIL_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_API_DETAIL_FAILURE, params);
      throw err;
    }
  },
  async delApi(context, params) {
    try {
      const res = await apiService.delApi(params);
      return res;
    } catch (err) {
      throw err;
    }
  },
  async unPublishApi({commit}, params) {
    return apiService.unPublishApi({ids: params, status: 0});
  },
  async getGroupList({commit}, params) {
    try {
      commit(GET_GROUP_LIST, params);
      const res = await apiService.getGroupList(params);
      commit(GET_GROUP_LIST_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_GROUP_LIST_FAILURE, err);
      throw err;
    }
  }
};

const mutations = {
  // 暂存数据到vuex
  [STORE_API_MSG](state, payload) {
    state.storeApiMsg = {
      ...state.storeApiMsg,
      ...payload
    };
  },
  [GET_API_DETAIL](state, params) {
    state.apiDetail.status = LOADING;
  },
  [GET_API_DETAIL_SUCCESS](state, payload) {
    state.apiDetail = {
      data: payload,
      status: SUCCESS
    };
  },
  [GET_API_DETAIL_FAILURE](state) {
    state.apiDetail = {
      ...state.apiDetail,
      status: FAIL
    };
  },
  [GET_GROUP_LIST](state, params) {
    state.sourceGroupTreeStatus = LOADING;
  },
  [GET_GROUP_LIST_SUCCESS](state, payload) {
    state.sourceGroupTree = payload;
    state.sourceGroupTreeStatus = SUCCESS;
  },
  [GET_GROUP_LIST_FAILURE](state, payload) {
    state.sourceGroupTree = payload;
    state.sourceGroupTreeStatus = FAIL;
  }
};

export default {
  namespaced: true,
  state,
  actions,
  mutations
};



